$cam_height: 45vh;

.main-menu {
    position: fixed;
    top: 0;
    height: 100%;
    width: 100%;



    .buttons-top {
        position: fixed;
        width: 100%;
        display: flex;
        justify-content: flex-end;
        padding: 2vh;
        z-index: 20;
        pointer-events: none;
        box-sizing: border-box; //argh this is already being inherited but i still need it
        .button {
            pointer-events: auto;
            width: 6vh;
            padding: 2.5vh;
            display: flex;
            align-items: center;
            justify-content: center;
            img {
                transform: scale(2,2) translate(0, 0.5px); } }

        .info-button {
            margin-right: auto; }

        .settings-button {
            margin-right: 2vh;
            img {
                transform: scale(1.8,1.8); } }

        .save-button {
            img {
                transform: scale(1.5,1.5); } } }






    .wrapper-emotion {
        position: relative;
        display: flex;
        flex-wrap: wrap-reverse;
        justify-content: center;
        width: 80vw;
        max-height: 80vh;
        overflow: visible;
        margin: auto;
        margin-top: 5vh;

        .button {
            margin: 1.2vh;
            border-radius: 6vh;
            .button-content {
                width: 100%;
                display: flex;
                flex-direction: column;
                overflow: visible;
                align-items: center;
                img {
                    width: calc(65vw / 5);
                    height: calc(75vh / 3);
                    object-fit: contain;
                    transform: scale(1.2,1.2);
                    //disables selecting
                    -webkit-user-drag: none;
                    pointer-events: none; } } } }



    .buttons-bottom {
        display: flex;
        justify-content: center;
        width: 100%;
        position: absolute;

        top: 88vh;
        .camera-button {
            display: flex;
            height: 2vh;
            justify-content: space-between;
            padding-right: 0;
            align-items: center;
            p {
                margin-right: 1vh; }
            img {
                max-width: none;
                max-height: none;
                width: 8vh;
                height: 8vh;
                margin-right: 1vh;
                transform: translate(0, 2px); } } } }





.camera-wrapper {
    position: absolute;
    top: 105vh;
    width: 100%;
    display: flex;
    justify-content: center;
    .camera-border {
        display: flex;
        justify-content: center;
        align-items: center;

        $cam_width: calc((#{$cam_height}/9) * 16);
        height: calc(#{$cam_height} + 7vh);
        width: calc(#{$cam_width} + 7vh);


        border: $stroke-weight solid black;
        border-radius: 6vh;

        .camera {
            height: $cam_height;
            width: calc((#{$cam_height}/9) * 16);

            border: $stroke-weight solid black;
            border-radius: 3.5vh; } } }

.donut-wrapper {
    position: absolute;
    top: 165vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    .inner-donut-wrapper {
        width: 15vh;
        object-fit: contain;
        position: relative;
        img {
            width: 100%;
            position: absolute;
            object-fit: contain;
            top: 0;
            transform: scale(0.6,0.6); }
        .donut {
            top: 0;
            position: absolute;
            object-fit: contain;
            width: 100%;
            transform: rotate(-90deg)  scale(3, 3); } } }

